@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header{
    height: 100vh;
    padding: top rem ;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*-----------CTA-----------*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/*-----------HEADER SOCIAL-----------*/
.header_social{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0%;
    bottom: 3rem;
}

/* line after social icons */
.header_social::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-promary);
}

/*-----------HEADER PIC1-----------*/
.Pic1 {
    background: linear-gradient(var(--color-promary),transparent);
    width: 26rem;
    height: 26rem;
    position: absolute;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 14rem 14rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/*-----------SCROLL DOWN-----------*/
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
/*----------------MEDIA QUERIES (MEDIUM-TABLETS)----------------*/

@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }
}

/*----------------MEDIA QUERIES (SMALL-MOBILES)----------------*/

@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }

    .header_social, .scroll_down{
        display: none;
    }
}

nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

vav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}

.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg, 
        transparent,
        var(--color-promary)
    );
    display: grid;
    place-items: center;    
}

.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition:var(--transition);
}

.about_me-image:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about_cards{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about_card{
    background: var(--color-bg-variant);
    border:1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);

}

.about_card:hover{
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.about_icon{
    color: var(--color-promary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5{
    font-size: 0.95rem;
}

.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about_content p {
    margin:2rem 0 2.6rem;
    color: var(--color-light);
}

/*----------------MEDIA QUERIES (MEDIUM-TABLETS)----------------*/

@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p{
        margin: 1rem 0 1.5rem;
    }
}

/*----------------MEDIA QUERIES (SMALL-MOBILES)----------------*/

@media screen and (max-width: 600px){
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about_content{
        text-align: center;
    }
    .about_content p{
        margin: 1.5rem 0;
    }
}

.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience_container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience_container>div:hover{
    background: transparent;
    border-color: var(--color-promary-variant);
    cursor: default;
}

.experience_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-promary);
}

.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
}

.experience_details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience_details-icon{
    margin-top: 6px;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience_details-icon{
    margin-top: 6px;
    color: var(--color-promary);
}

/*----------------MEDIA QUERIES (MEDIUM-TABLETS)----------------*/

@media screen and (max-width: 1024px){
    .experience_container{
        grid-template-columns: 1fr;
    }
    .experience_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
}

/*----------------MEDIA QUERIES (SMALL-MOBILES)----------------*/

@media screen and (max-width: 600px){
    .experience_container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience_container > div{
        width: 100%;
        padding:2rem 1 rem ;
    }
}


