*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* css variables */
:root{
    --color-bg: #192b3c;
    --color-bg-variant: #212c5a;
    --color-promary-variant:#64BABF;
    --color-promary: #4db5ff;
    --color-promary2: #FEF63F;
    --color-white: #ffff;
    --color-black: #000000;
    --color-light: rgba(255, 255, 255, 0.6);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition:400 ms;
}

html{
    scroll-behavior:  smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: #192b3c;
    background: var(--color-bg);
    color: #ffff;
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assets/bg-texture.png); */
}

/* -----------------GENERAL STYLES---------------- */

.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;   
}

section > h2, section >h5{
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section>h2{
    color: #FEF63F;
    color: var(--color-promary2);
    margin-bottom: 3rem;
}

.text-light {
    color: #4db5ff;
    color: var(--color-promary);
    transition: 400 ms;
    transition: var(--transition);
}

a{
    color: #4db5ff;
    color: var(--color-promary);
    transition: 400 ms;
    transition: var(--transition);
}

a:hover{
    color: #ffff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #FEF63F;
    color: var(--color-promary2);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #FEF63F;
    border: 1px solid var(--color-promary2);
    transition: 400 ms;
    transition: var(--transition);
}

.btn:hover{
    background: #ffff;
    background: var(--color-white);
    color: #192b3c;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: #FEF63F;
    background: var(--color-promary2);
    color: #192b3c;
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/*----------------MEDIA QUERIES (MEDIUM-TABLETS)----------------*/

@media screen and (max-width: 1024px){
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/*----------------MEDIA QUERIES (SMALL-MOBILES)----------------*/

@media screen and (max-width: 600px){
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section>h2{
        margin-bottom: 2rem;
    }
}
